@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700);
:root {
  --app-color: #563636;
  --app-semi-color: #56363685;
}
body
{
    font-family: 'Roboto', sans-serif !important;
}

.app-container{
  
}
.app-container .app-content{
  margin:auto;
  max-width: 1140px;
}
.app-header{
    background-color: var(--app-color);
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 9999;
}
.app-header .app-header-content{
    display: flex;
    align-items: center;
}
.app-header .app-header-content .app-logo{
    font-size: 2rem;
    color: white;
    font-weight: bold;
    flex: 1 1;
}
.app-header .app-header-content .app-logo a{
    padding:0;
}
.app-header .app-header-content .nav-link{
    color:white;
}
.book-filter__container{
    padding: 20px 0px;
    background-color: var(--app-semi-color);
}
.book-filter__container .title{
    font-size: 2rem;
    font-weight: 500;
    text-align: center;
}
.book-filter__container .sub-title{
    text-align: center;
}
.book-filter__container .search-bar{
    position: relative;
    margin-top: 10px;
    padding: 15px;
    /* border-radius: 25px; */
    background-color: white;
    display: flex;
}
.book-filter__container .search-bar .icon{
    width: 30px;
}
.book-filter__container .search-bar .search-input{
    font-size: 1.3rem;
    border: none;
    width: 100%;
    flex: 1 1;
    margin-left: 10px;
}
.book-filter__container .search-bar .search-input:focus{
    outline: none;
}

.book-filter__container .search-filter{
    /* border-radius: 25px; */
    padding-top: 10px;
    background-color: white;
}

.product-card {
    width: 100%;
    position: relative;
    box-shadow: 0 2px 7px var(--app-semi-color);
    margin: 20px auto;
    background: #fafafa;
    animation: fadeIn ease 2s;
    -webkit-animation: fadeIn ease 2s;
    -moz-animation: fadeIn ease 2s;
    -o-animation: fadeIn ease 2s;
    -ms-animation: fadeIn ease 2s;
}

.badge {
    position: absolute;
    left: 0;
    top: 20px;
    text-transform: uppercase;
    font-size: 13px;
    font-weight: 700;
    background: red;
    color: #fff;
    padding: 3px 10px;
}

.product-tumb {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 300px;
    padding: 20px;
    background: #f0f0f0;
}
.product-tumb:hover{
    cursor: pointer;
    opacity: 0.5;
}
.product-tumb:hover img{
    -webkit-transform: scale(1.4);
            transform: scale(1.4);
}
.product-tumb img {
    max-width: 100%;
    max-height: 100%;
}

.product-details {
    padding: 30px;
}

.product-catagory {
    display: block;
    font-size: 12px;
    font-weight: 700;
    text-transform: uppercase;
    color: rgb(49, 45, 45);
    margin-bottom: 18px;
    min-height: 40px;
}

.product-details h4 a {
    font-weight: 500;
    display: block;
    margin-bottom: 18px;
    text-transform: uppercase;
    color: #363636;
    text-decoration: none;
    transition: 0.3s;
}

.product-details h4 a:hover {
    color: #fbb72c;
}

.product-details p {
    font-size: 15px;
    line-height: 22px;
    margin-bottom: 18px;
    color: #999;
}

.product-bottom-details {
    overflow: hidden;
    border-top: 1px solid #eee;
    padding-top: 20px;
}

.product-bottom-details div {
    float: left;
    width: 50%;
}

.product-price {
    font-size: 18px;
    color: #fbb72c;
    font-weight: 600;
}

.product-price small {
    font-size: 80%;
    font-weight: 400;
    text-decoration: line-through;
    display: inline-block;
    margin-right: 5px;
}

.product-links {
    text-align: right;
}

.product-links a {
    display: inline-block;
    margin-left: 5px;
    color: #e1e1e1;
    transition: 0.3s;
    font-size: 17px;
}

.product-links a:hover {
    color: #fbb72c;
}

@keyframes fadeIn{
    0% {
        opacity:0;
    }
    100% {
        opacity:1;
    }
}

@-webkit-keyframes fadeIn {
    0% {
        opacity:0;
    }
    100% {
        opacity:1;
    }
}


.add-book__header{
    padding:10px;
    background-color: var(--app-semi-color);
    text-align: center;
    color: white;
}

.new-book__item{
    border: 1px solid var(--app-color);
    border-left: 5px solid var(--app-color);
    padding: 10px; 
    margin: 20px 0px;
    position: relative;
}
.new-book__item .del-book__badge{
    position: absolute;
    right: 5px;
    top: 2px;
    font-size: 1.2rem;
    background-color: var(--app-semi-color);
    border-radius: 50%;
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--app-color);
}
.new-book__item .del-book__badge:hover{
    cursor: pointer;
    color:white;
}
.app-footer{
    background-color: var(--app-semi-color);
    min-height: 100px;
    margin-top: 20px;
}
